body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #909ba3;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100vh;
  overflow-x: hidden;
}
* {
  box-sizing: border-box;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
::-webkit-scrollbar {
  width: 4px;
  background: rgba(0, 0, 0, 0);
}
/* Track */
::-webkit-scrollbar-track {
  background: rgb(0, 0, 0);
}
/* Handle */
::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background: #d4d4d4;
}
/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  width: 5px;
  background: #57bdeb;
  box-shadow: 0 0 5px black;
  border-radius: 10px;
}

::-moz-selection {
  /* Code for Firefox */
  color: #57bdeb;
  background: black;
}

::selection {
  color: white;
  background: #446ee2;
}
ul {
  list-style: none;
  padding: 0;
}
.app {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background-color: #272b2e;
  max-width: 768px;
  width: 768px;
}
.navbar {
  background-color: #141414;
  height: 76px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  color: #57bdeb;
}
.navbar .logo {
  font-size: 20px;
}
.navbar button {
  border: 0;
  background-color: #00000000;
  color: white;
}
.messages {
  overflow-y: scroll;
  overflow-x: hidden;
  height: 500px;
}
.msg {
  display: grid;
  grid-template-columns: 20% 80%;
  margin: 20px 0;
}
.msg .img img {
  border-radius: 50%;
}
.msg .img {
  display: flex;
  align-items: center;
  justify-content: center;
}
.header {
  display: flex;
  flex-direction: row;
}
.header .name {
  color: #57bdeb;
  font-weight: bold;
}
.header .date {
  color: rgba(255, 255, 255, 0.652);
  margin-left: 20px;
}
.content {
  color: white;
  font-size: 19px;
}
.channel {
}
.inpt-msg {
  width: 90%;
  height: 51px;
  border-radius: 15px;
}
.send-btn {
  background-color: #00000000;
  border: 0;
}
.form {
  background-color: #141414;
  height: 76px;
  display: grid;
  grid-template-columns: 80% 20%;
  align-items: center;
  justify-items: center;
}
@media screen and (max-width: 768px) {
  .app {
    width: 100vw;
    height: 100vh;
  }
}
